/* eslint global-require: 0, flowtype-errors/show-errors: 0 */

/**
 * This module executes inside of electron's main process. You can start
 * electron renderer process from here and communicate with the other processes
 * through IPC.
 *
 * When running `yarn build` or `yarn build-main`, this file is compiled to
 * `./app/main.prod.js` using webpack. This gives us some performance wins.
 *
 * @flow
 */
import { app, BrowserWindow, ipcMain } from 'electron';
import MenuBuilder from './menu';
import path from 'path';

if (process.env.NODE_ENV === 'production') {
  const sourceMapSupport = require('source-map-support');
  sourceMapSupport.install();
}

if (
  process.env.NODE_ENV === 'development' ||
  process.env.DEBUG_PROD === 'true'
) {
  require('electron-debug')();
  const path = require('path');
  const p = path.join(__dirname, '..', 'app', 'node_modules');
  require('module').globalPaths.push(p);
}

const TrayWindow  = require('./windows/Tray/TrayWindow');
const TrayIcon    = require('./windows/Tray/TrayIcon');

let mainWindow = null;
let trayWindow = null;
let trayIcon = null;


const installExtensions = async () => {
  const installer = require('electron-devtools-installer');
  const forceDownload = !!process.env.UPGRADE_EXTENSIONS;
  const extensions = ['REACT_DEVELOPER_TOOLS', 'REDUX_DEVTOOLS'];

  return Promise.all(
    extensions.map(name => installer.default(installer[name], forceDownload))
  ).catch(console.log);
};

/**
 * Add event listeners...
 */

app.on('window-all-closed', () => {
  // Respect the OSX convention of having the application in memory even
  // after all windows have been closed
  if (process.platform !== 'darwin') {
    app.quit();
  }
});

app.on('ready', async () => {
  if (
    process.env.NODE_ENV === 'development' ||
    process.env.DEBUG_PROD === 'true'
  ) {
    await installExtensions();
  }

  mainWindow = new BrowserWindow({
    titleBarStyle: 'hidden',
    width: 372,
    height: 538,
    minWidth: 372,
    minHeight: 538,
    icon: path.resolve(path.join(__dirname, './public/icons/app_icon.png')),
    show: true,
    frame: false,
  });

  trayWindow = new TrayWindow();
  trayIcon = new TrayIcon(trayWindow.window, mainWindow);

  // mainWindow.on('blur', () => {
  //   mainWindow.hide();
  // });

  mainWindow.loadURL(`file://${__dirname}/index.html`);

  // @TODO: Use 'ready-to-show' event
  //        https://github.com/electron/electron/blob/master/docs/api/browser-window.md#using-ready-to-show-event
  mainWindow.webContents.on('did-finish-load', () => {

    if (!mainWindow) {
      throw new Error('"mainWindow" is not defined');
    }
    if (process.env.START_MINIMIZED) {
      mainWindow.minimize();
    } else {
      mainWindow.show();
      mainWindow.focus();
    }
  });

  const menuBuilder = new MenuBuilder(mainWindow);
  menuBuilder.buildMenu();

  mainWindow.on('closed', () => {
    mainWindow = null;
  });


  const hideWindow = () => {
    if(mainWindow!=null) {
      if(mainWindow.isVisible()) {
        mainWindow.hide();
        //***** mac *****
        // app.dock.hide();
      }
    }
  } 

  const showWindow = () => {
    if(mainWindow!=null) {
      if(!mainWindow.isVisible()) {
        mainWindow.show();
        // ***** mac *****
        // app.dock.show();
        // ***** windwos *****
      }
    }
  }
  ipcMain.on('show-window', () => {
    showWindow()
  })

  ipcMain.on('hide-window', () => {
    hideWindow()
  })

  ipcMain.on('signout-window', () => {
    trayWindow.window.hide()
  })

  ipcMain.on('quit-app', function() {
    app.quit();
  });
});
